.sign{
  width: 80%;
  margin:0 auto;
}

.sign::after{
  content:"";
  display:block;
  clear:both;
}

.sign-in{
  float: left;
}
.sign-up{
  float:right;
}

.sign-in,.sign-up,.sign-in:hover,.sign-in:focus.sign-up:hover,.sign-up:focus{
  outline: none;
  border: none;
}

.sign-in{
  font-size: 24px;
  height: 46px;
  line-height: 46px;
  padding: 0 14%;
  /* margin: 0 6%; */
  background:#66BFFF;
  border-radius: 5px;
  color:#fff;
}
.sign-up{
  font-size: 24px;
  height: 46px;
  line-height: 46px;
  padding: 0 12%;
  /* margin: 0 6%; */
  background:#999999;
  border-radius: 5px;
  color:#fff;
}
