.page-header{
  background: #fff;
  position: fixed;
  top:0;
  width: 100%;
  font-weight: bold;
  font-size:28px;
  padding: 20px;
  color:#33333D;
}

.page-content{
  margin-top: 96px;
  padding: 0 20px;
}

.input-title{
  font-size: 22px;
}
.input-area,.input-area:hover,.input-area:focus{
  outline: none;
  border: none;
}
.input-area{
  box-sizing: border-box;
  width: 100%;
  margin: 10px 0;
  padding: 0 10px;
  background: #F3F3F3;
  border: none;
  height: 42px;
  font-size: 18px;
  border-radius: 5px;
}
.input-button{
  margin: 0 auto;
  width: 90%;
  margin-top:20px ;
}
.input-button::after{
  content: "";
  display:block;
  clear:none;
}

.input-button-ok,
.input-button-ok:hover,
.input-button-ok:focus,
.input-button-return,
.input-button-return:hover,
.input-button-return:focus{
  outline: none;
  border: none;
}
.input-button-ok{
  font-size: 24px;
  height: 46px;
  line-height: 46px;
  padding: 0 13%;
  background:#66BFFF;
  border-radius: 5px;
  color:#fff;
}

.input-button-return{
  float: right;
  font-size: 24px;
  height: 46px;
  line-height: 46px;
  padding: 0 13%;
  background:#999999;
  border-radius: 5px;
  color:#fff;
}